@fontColor:#373d46;
@selectFontColor:#ffffff;
@themeColor:#047adf;
@themeBackground:#fafafa;
@thColor:#f5f5f5;
@trInterval:#f8fbff;
@trHover:#f9ffba;
@menuColor:#2f3a4c;
@headerHeight:124px;
@subHeaderHeight:44px;
@subHeaderColor:rgb(27,95,204);
@menuWidth:255px;
@menuItemHeight:50px;
@menuItemColor:#2d3846;
@menuSubItemColor:#262e3b;
@menuItemSelect:#047adf;
@subItemHeight:55px;
@tabWidth:150px;

/*----------------------------
            框架样式
 -----------------------------*/
html{
  font-family:"Microsoft Yahei","Droid Sans Fallback","Arial","Helvetica","sans-serif","宋体";font-size:14px;outline: none;color: @fontColor;
  background-color: @themeBackground;
}

.frame2{
  height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  >header{
    height:@headerHeight;
    z-index: 1;
    user-select: none;
    &.full-screen{
      height:@subHeaderHeight;
    }
  }
  .frame-content{
    display: flex;
    flex:1 1 auto;
    height: 1px;
    z-index: 0;
  }
  >footer{
    height:0;
  }
  .notify{
    position: absolute;
    right: 400px;
    width: 300px;
    align-self: center;
    top: 25px;
  }
  .notify.full-screen{
    top: 6px;
  }
}
.header-up{
  height:@headerHeight - @subHeaderHeight;
  display: flex;
  -webkit-transition: all 300ms;
  -moz-transition: all 300ms;
  -ms-transition: all 300ms;
  -o-transition: all 300ms;
  transition: all 300ms;
  &.full-screen{
    height: 0;
    *{
      display:none;
    }
  }
  .system-info{
    color: @selectFontColor;
    display: flex;
    .logo img{
      height:100%;
      width: 80px;
    }
    .system-name{
      padding: 16px 0 0 10px;
      >div:first-child{
        font-size: 20px;
        font-weight: bold;
      }
    }
  }
  .tools{
    flex:1;
    .theme-option{
      height: 100%;
      color: white;
      text-align: right;
      padding-right: 30px;
      display: flex;
      .dropdown{
          .dropdown-toggle{
            line-height: 80px;
            margin-left: 10px;
            flex: 1;
            padding: 0 10px;
            cursor: pointer;
            height: 80px;
          }
          ul{
              li{
                  .theme-item{
                    float: left;
                    background: red;
                    border-radius: 50px;
                    padding: 2px;
                    border: 2px solid white;
                    margin-right: 5px;
                    height: 20px;
                    width: 20px;
                    align-self: center;
                    text-align: center;
                    cursor: pointer;
                    i{
                      position: relative;
                      top: -3px;
                    }
                  }
              }
          }
      }
    }
  }
  .user-info{
    width: 180px;
    color:white;
    >.btn-group{
      display: flex;
      .user-img{
        height: 60px;
        width: 60px;
        background: aliceblue;
        border-radius: 30px;
        margin-top: 10px;
        >div{
          height: 54px;
          width: 54px;
          background: red;
          border-radius: 30px;
          margin: 3px;
        }
      }
      .dropdown-toggle{
        line-height: 80px;
        margin-left: 10px;
        flex: 1;
        padding: 0 10px;
        cursor: pointer;
        height: 80px;
        >span:first-child{
          max-width: 65px;
          display: inline-block;
          line-height: 80px;
        }
        >span:last-child{
          font-size: 12px;
          padding-left: 5px;
          display: inline-block;
          position: absolute;
          top: 33px;
        }
      }
    }
  }
}
.header-down{
  height:@subHeaderHeight;
  background:@subHeaderColor;
  box-shadow: 0px 2px 5px rgba(0,0,0,.15);
  display: flex;
  .menu-header{
    width: @menuWidth;
    background-color: @menuItemColor;
    text-align: center;
    text-transform: uppercase;
    color: @selectFontColor;
    padding-top: 13px;
    position: relative;
    z-index: 2;
    pointer-events: none;
    &.full-screen{
      width:100px;
      cursor: pointer;
      pointer-events: initial;
      &:hover{
        background-color: @menuItemSelect;
      }
    }
  }
  .tab-header{
    flex:1;
    display: flex;
    .tool-item{
      color: rgb(133,180,236);
      width: 30px;
      text-align: center;
      cursor: pointer;
      background: #1b5fcc;
      position: relative;
      z-index: 1;
      i{
        line-height: 44px;
        font-size: 15px;
      }
      &:hover{
        background-color: rgb(2,81,174);
      }
    }
    .tool-tab{
      flex:1;
    }
  }
}
.frame-content{
  .menu{
    height:100%;
    width:@menuWidth;
    cursor: pointer;
    user-select: none;
    -webkit-transition: all 300ms;
    -moz-transition: all 300ms;
    -ms-transition: all 300ms;
    -o-transition: all 300ms;
    transition: all 300ms;
    overflow-y: auto;
    overflow-x: hidden;
    &.full-screen{
      position: absolute;
      z-index: 999;
      top: 44px;
    }
    &.hidden1{
      transform:translateY(-120%);
    }
    .menu-item{
      >.nav{
        height: 63px;
        font-size: 14px;
        padding: 20px 0 0 30px;
        >i{
          font-size: 20px;
        }
        >.nav-icon{
          height: 20px;
          margin-top: -6px;
        }
        >.title{
          padding-left: 10px;
        }
        >span.icon{
          float: right;
          position: relative;
          top: 5px;
          right: 30px;
          -webkit-transition: all 300ms;
          -moz-transition: all 300ms;
          -ms-transition: all 300ms;
          -o-transition: all 300ms;
          transition: all 300ms;
          &.click-down{
            -webkit-transform: rotate(180deg);
            -ms-transform: rotate(180deg);
            -o-transform: rotate(180deg);
            transform: rotate(180deg);
          }
        }
      }
      .sub-item{
        list-style-type: none;
        margin: 0;
        padding: 0;
        -webkit-transition: all 300ms;
        -moz-transition: all 300ms;
        -ms-transition: all 300ms;
        -o-transition: all 300ms;
        transition: all 300ms;
        &.hidden1{
          height: 0 !important;
          li{
            display: none;
          }
        }
        >li{
          width: @menuWidth;
          height:@subItemHeight;
          display: flex;
          padding-top: 18px;
          .subitem-icon{
            padding-left: 50px;
            .nav-icon{
              height:15px;
            }
          }
          .subitem-title{
            flex: 1;
            padding-left: 10px;
          }
          &:hover{
            background-color: @menuItemSelect;
          }
          &.active{
            background-color: @menuItemSelect;
          }
        }
      }
    }
  }
  .content{
    margin: 10px;
    -webkit-box-shadow: 0 0 5px #eaeaea;
    -moz-box-shadow: 0 0 5px #eaeaea;
    box-shadow: 0 0 5px #eaeaea;
    flex: 1;
    border: 1px solid #dfdfdf;
    height:calc(~"100% - 15px");
    width:500px;
    background: white;
    position: relative;
    .view-content{
      height: 100%;
      overflow-y: auto;
      overflow-x: hidden;
      >div{
        height:100%;
      }
    }
  }
}

/*----------------------------
            常用页面样式
 -----------------------------*/
.under-design{
  height: 100%;
  background: #a6c3de;
  text-align: center;
  display: flex;
  .page-content{
    margin: auto;
  }
  i{
    font-size: 120px;
    color: rgba(0,0,0,0.2);
    align-self: center;
  }
  .title{
    color: #FFF;
    font-size: 60px;
    text-align: center;
  }
}


/*----------------------------
            通用样式
 -----------------------------*/
.btn{
  border-radius: 0;
  min-width: 90px;
}
.ui-grid-top-panel{
  background: @thColor !important;
}

/*----------------------------
            插件补充样式
 -----------------------------*/
#loading-bar .bar {
  top: auto !important;
  left: auto !important;
  background-color: red;
}
#loading-bar-spinner {
  //background-color: red;
  color:red;
  top: auto !important;
  left: auto !important;
}
.fr-box.fr-basic .fr-element {
  white-space: normal;
  word-break: break-all;
}

/*----------------------------
            兼容低分屏样式
 -----------------------------*/
@media screen and (min-width: 1366px) and (max-width: 1600px){
  .query-form>header>h4{
    padding: 10px;
  }
  .ngdialog.ngdialog-theme-default{
    padding-top: 80px;
    padding-bottom: 80px;
  }
}
.pageload-overlay{
  position:absolute;
}
.pageload-overlay::after, .pageload-overlay::before{
  position:absolute;
}


@media screen and (max-width: 1366px) {
  .frame2 > header{
    height:114px !important;
  }
  .frame2 > header.full-screen {
    height: 44px !important;
  }
  .header-up{
    height:70px !important;
    .system-info .logo img{
      width:70px !important;
    }
    .tools .theme-option > span{
      line-height: 70px !important;
    }
    .user-info > .btn-group {
      .user-img{
        margin-top:5px !important;
      }
      .dropdown-toggle{
        line-height: 70px !important;
        height: 70px !important;
      }
    }

  }
  .header-up.full-screen {
    height: 0 !important;
  }
  .service-header{
    div:nth-child(1){
      width: 200px !important;
    }
  }
  .service-center{
    .service-tree{
      width:200px !important;
    }
  }

  .frame-content .menu .menu-item > {
    .nav{
      height:44px !important;
      padding: 12px 0px 0px 30px !important;
    }
    .sub-item > li{
      height:44px !important;
      padding-top:12px !important;
    }
  }
  .query-form {
    header h4{
      padding:10px 0px 10px 10px !important;
    }
    .query-content{
      padding: 0px 8px !important;
      .tool-bar{
        height:44px !important;
      }
      .query-condition{
        padding:8px 0px 0px 0px!important;
        .query-tool{
          padding-left:40px !important;
        }
      }
      .tool-bar .tool-tag .tag-container{
        margin:auto !important;
      }
      .tool-buttons{
        .btn-link{
          font-size: 14px !important;
          border: none !important;
        }
      }
      .content-table{
        table> tbody > tr:nth-child(even) > td, table> tbody > tr:nth-child(even) > th {
          background-color: #f9f9f9;
        }
        .table-striped > tbody > tr:nth-child(odd) > td, .table-striped > tbody > tr:nth-child(odd) > th{
          background-color: #ffffff !important;
        }
      }
      .app-content ul li > span > div > div {
        > div.title{
          height:25px !important;
          line-height: 25px !important;
        }
        > div.image{
          width:40% !important;
          padding-top:40% !important;
        }
        >div.image:hover{
          width: 45% !important;
          padding-top: 45% !important;
        }
      }
    }
  }
  .ngdialog.ngdialog-theme-default.custom.sm .ngdialog-content{
    width: 570px !important;
  }
  .ngdialog.ngdialog-theme-default.custom .ngdialog-content {
    width: 900px !important;
    height:450px !important;
    .dialog-header{
      padding:10px !important;
    }
    .dialog-content{
      height:300px !important;
      padding:0px 30px 10px 30px!important;
    }
    .nav-tabs{
      margin:0px 10px;
    }
    .gm-pagination{
      .pager-loose{
        margin:10px 0px !important;
        line-height: 1em;
        > li > a{
          padding:3px 9px !important;
        }
      }
      .page-tool{
        margin: 13px 0 0 10px !important;
      }
    }
    .form-simple {
      .table{
        td.td-label{
          padding: 13px 8px 0px 8px !important;
          label{
            margin-bottom: 0px !important;
          }
        }
        td.td-value{
          padding:13px 50px 0px 8px !important;
          .form-control{
            height:25px !important;
            line-height: 1 !important;
          }
          .gm-tree-div{
            min-height:50px !important;
            .tree-a{
              font-size: 13px !important;
            }
          }
        }
      }
      tr.catagory span{
        margin-top:10px !important;
      }
    }
  }

}
.theme-choosed{
    >a{
        color: #ffffff !important;
    }
    background: #145ccd;
}



