/*-------------------
        蓝色主题
 --------------------*/
html.blue {
  color: #373d46;
  background-color: #fafafa;
  /*----------------------------------
          控件主题
 ----------------------------------*/
  /*-----------------------
        进度条样式
    ------------------------*/
}
html.blue .header-up {
  background: #047adf url(img/head-bg.png) no-repeat 100% 130%;
}
html.blue .header-up .system-info {
  color: #ffffff;
}
html.blue .header-up .user-info {
  color: #ffffff;
}
html.blue .header-down {
  background: #1b5fcc;
}
html.blue .header-down .menu-header {
  background-color: #2d3846;
  color: #ffffff;
}
html.blue .header-down .menu-header.full-screen:hover {
  background-color: #047adf;
}
html.blue .header-down .tab-header .tool-item {
  background: #0e6deb;
  color: #85b4ec;
}
html.blue .header-down .tab-header .tool-item:hover {
  background-color: #0251ae;
}
html.blue .frame-content .menu {
  background-color: #2f3a4c;
  color: #ffffff;
}
html.blue .frame-content .menu .menu-item > .nav {
  height: 63px;
  font-size: 14px;
  padding: 20px 0 0 30px;
  border-bottom: 1px dashed #3c4759;
}
html.blue .frame-content .menu .menu-item > .nav > i {
  color: #fff;
}
html.blue .frame-content .menu .menu-item > .nav > span.icon {
  color: #ffffff;
}
html.blue .frame-content .menu .menu-item > .nav:hover {
  background: none;
}
html.blue .frame-content .menu .menu-item > .nav.active {
  background: none;
}
html.blue .frame-content .menu .menu-item > .nav.active > i {
  color: #ffffff;
}
html.blue .frame-content .menu .menu-item .sub-item > li {
  border-bottom: 1px dashed #3c4759;
  background-color: #262e3b;
}
html.blue .frame-content .menu .menu-item .sub-item > li:hover {
  background-color: #047adf;
}
html.blue .frame-content .menu .menu-item .sub-item > li.active {
  background-color: #047adf;
}
html.blue .frame-content .menu .menu-item .sub-item > li .subitem-icon {
  color: #fff;
}
html.blue .tab-wrapper .tab-tabs ul > li {
  background: #0e6deb;
  color: white;
}
html.blue .tab-wrapper .tab-tabs ul > li.active {
  background: #0251ae;
  border-top: 2px solid #02e101;
}
html.blue .tab-wrapper .tab-tabs ul > li .close-button:hover {
  color: #fff;
}
html.blue .tab-wrapper .tab-tool {
  color: #85b4ec;
  background: #1b5fcc;
}
html.blue .tab-wrapper .tab-tool:hover {
  background-color: #0251ae;
}
html.blue .tab-wrapper .tab-tool.disabled:hover {
  background: #1b5fcc;
}
html.blue #loading-bar .bar {
  background-color: #047adf;
}
html.blue #loading-bar-spinner .spinner-icon {
  border-top-color: #047adf;
  border-left-color: #047adf;
}



