/*-------------------
        红色主题
 --------------------*/
html.red {
  color: black;
  background-color: #fafafa;
  /*----------------------------------
          控件主题
 ----------------------------------*/
  /*-----------------------
        进度条样式
 ------------------------*/
}
html.red .header-up {
  background: url(img/head-red.png) no-repeat 100% 130%;
}
html.red .header-up .system-info {
  color: white;
}
html.red .header-up .user-info {
  color: white;
}
html.red .header-down {
  background: #f0f0f0;
}
html.red .header-down .menu-header {
  background-color: white;
  color: black;
}
html.red .header-down .menu-header.full-screen:hover {
  background-color: #f7f7f7;
}
html.red .header-down .tab-header .tool-item {
  background: #dd6b66;
  color: white;
}
html.red .header-down .tab-header .tool-item:hover {
  background: #e01f54;
}
html.red .frame-content .menu {
  background-color: white;
  color: black;
}
html.red .frame-content .menu .menu-item > .nav {
  height: 63px;
  font-size: 14px;
  padding: 20px 0 0 30px;
  border-bottom: 1px dashed #3c4759;
}
html.red .frame-content .menu .menu-item > .nav > i {
  color: black;
}
html.red .frame-content .menu .menu-item > .nav > span.icon {
  color: none;
}
html.red .frame-content .menu .menu-item > .nav:hover {
  background: none;
}
html.red .frame-content .menu .menu-item > .nav.active {
  background: none;
}
html.red .frame-content .menu .menu-item > .nav.active > i {
  color: black;
}
html.red .frame-content .menu .menu-item .sub-item > li {
  border-bottom: 1px dashed #3c4759;
  background-color: white;
}
html.red .frame-content .menu .menu-item .sub-item > li:hover {
  background-color: #f7f7f7;
}
html.red .frame-content .menu .menu-item .sub-item > li.active {
  background-color: #f7f7f7;
}
html.red .frame-content .menu .menu-item .sub-item > li .subitem-icon {
  color: black;
}
html.red .tab-wrapper .tab-tabs ul > li {
  background: #dd6b66;
  color: white;
}
html.red .tab-wrapper .tab-tabs ul > li.active {
  background: #e01f54;
  border-top: 2px solid #f5ab05;
}
html.red .tab-wrapper .tab-tabs ul > li .close-button:hover {
  color: #fff;
}
html.red .tab-wrapper .tab-tool {
  color: #85b4ec;
  background: #1b5fcc;
}
html.red .tab-wrapper .tab-tool:hover {
  background-color: #0251ae;
}
html.red .tab-wrapper .tab-tool.disabled:hover {
  background: #1b5fcc;
}
html.red #loading-bar .bar {
  background-color: #047adf;
}
html.red #loading-bar-spinner .spinner-icon {
  border-top-color: #047adf;
  border-left-color: #047adf;
}



