@fontColor: black;
@selectFontColor: white;
@themeColor: #047adf;
@themeBackground: #fafafa;
@thColor: #f5f5f5;
@trInterval: #f8fbff;
@trHover: #f9ffba;
@menuColor: white;
@subHeaderColor: rgb(240, 240, 240);
@menuItemColor: black;
@menuSubItemColor: black;
@menuItemSelect: rgb(247, 247, 247);


/*-------------------
        红色主题
 --------------------*/
html.red{
    color: @fontColor;
    background-color: @themeBackground;
    .header-up {
        background: url(img/head-red.png) no-repeat 100% 130%;
        .system-info {
            color: @selectFontColor;
        }
        .user-info {
            color: @selectFontColor;
        }
    }
    .header-down {
        background: @subHeaderColor;
        .menu-header {
            background-color: white;
            color: black;
            &.full-screen:hover {
                background-color: @menuItemSelect;
            }
        }
        .tab-header {
            .tool-item {
                background: #dd6b66;
                color: @selectFontColor;
                &:hover {
                    background: #e01f54;
                }
            }
        }
    }

    .frame-content {
        .menu {
            background-color: white;
            color: black;
            .menu-item {
                >.nav {
                    height: 63px;
                    font-size: 14px;
                    padding: 20px 0 0 30px;
                    border-bottom: 1px dashed rgb(60, 71, 89);
                    >i {
                        color: @fontColor;
                    }
                    >span.icon {
                        color: none;
                    }
                }
                >.nav:hover {
                    background: none;
                }
                >.nav.active {
                    background: none;
                    >i {
                        color: @fontColor;
                    }
                }
                .sub-item {
                    >li {
                        border-bottom: 1px dashed rgb(60, 71, 89);
                        background-color: white;
                        &:hover {
                            background-color: @menuItemSelect;
                        }
                        &.active {
                            background-color: @menuItemSelect;
                        }
                        .subitem-icon {
                            color: @fontColor ;
                        }
                    }
                }
            }
        }
    }
    /*----------------------------------
          控件主题
 ----------------------------------*/
    .tab-wrapper {
        .tab-tabs {
            ul>li {
                background: #dd6b66;
                color: white;
                &.active {
                    background: #e01f54;
                    border-top: 2px solid #f5ab05;
                }
                .close-button:hover {
                    color: #fff;
                }
            }
        }
        .tab-tool {
            color: rgb(133, 180, 236);
            background: #1b5fcc;
            &:hover {
                background-color: rgb(2, 81, 174);
            }
            &.disabled {
                &:hover {
                    background: #1b5fcc;
                }
            }
        }
    }
    /*-----------------------
        进度条样式
 ------------------------*/
    #loading-bar .bar {
        background-color: @themeColor;
    }
    #loading-bar-spinner .spinner-icon {
        border-top-color: @themeColor;
        border-left-color: @themeColor;
    }
}


